
.w-upload .el-upload{width:auto
}
.w-upload .el-upload__inner{display:inline-block;height:120px;width:120px;text-align:center;border-radius:4px;border:1px solid #E0E6ED;vertical-align:middle
}
.w-upload .el-icon-plus{color:#E0E6ED;font-size:32px;line-height:120px
}
.w-upload__files{display:inline-block
}
.w-upload__file{display:inline-block;height:120px;width:120px;text-align:center;border-radius:4px;border:1px solid #E0E6ED;position:relative;margin-right:20px
}
.w-upload__file img{display:block;width:100%;height:100%
}
.w-upload__file:hover .w-upload__file__cover{opacity:1
}
.w-upload__file__cover{position:absolute;bottom:0;left:0;opacity:0;width:100%;height:100%;background-color:rgba(0,0,0,0.72);text-align:center;transition:opacity 500ms ease
}
.w-upload__file__cover__btns .btn{display:inline-block;color:#fff;font-size:12px;cursor:pointer;vertical-align:middle;transition:transform .3s cubic-bezier(.23, 1, .32, 1) .1s,opacity .3s cubic-bezier(.23, 1, .32, 1) .1s;margin-top:35px
}
.w-upload__file__cover__btns .btn:hover{transform:translateY(-6px)
}
.w-upload__file__cover__btns .btn:not(:first-child){margin-left:16px
}
.w-upload__file__cover__btns .btn i{color:#fff;display:block;font-size:20px;line-height:inherit;margin:0 auto 5px
}
.w-upload_trigger{position:relative;display:inline-block;vertical-align:top
}
.w-upload__progress{position:absolute;left:0;right:0;top:0;bottom:0
}
.w-upload__popover__btns{text-align:right
}
.list-complete-item{transition:all 500ms
}
.list-complete-enter,.list-complete-leave-active{opacity:0;transform:translateY(20px)
}


