
.w-diamond-button{display:inline-block;margin:0;padding:0 30px;border:none;line-height:60px;font-size:16px;color:#fff;text-align:center;vertical-align:top;white-space:nowrap;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;background:#20a0ff;outline:0
