
.w-label-grid{font-size:0
}
.w-label-grid .el-checkbox,.w-label-grid .el-radio{width:33.33%;margin-bottom:30px
}
.w-label-grid .el-checkbox__input,.w-label-grid .el-radio__input{position:absolute;top:4px
}
.w-label-grid .el-checkbox__label,.w-label-grid .el-radio__label{display:inline-block;width:100%;vertical-align:top;overflow:hidden;text-overflow:ellipsis;padding-left:25px;padding-top:2px
}
.w-label-grid .el-tooltip{width:100%
}
.w-label-grid .el-tooltip__rel{width:100%;overflow:hidden;text-overflow:ellipsis
}
.w-label-grid .el-checkbox+.el-checkbox,.w-label-grid .el-radio+.el-radio{margin-left:0
}
.w-label-grid--c2 .el-checkbox,.w-label-grid--c2 .el-radio{width:50%
}
.w-label-grid--c4 .el-checkbox,.w-label-grid--c4 .el-radio{width:25%
}


