
.w-panel{position:relative;padding:30px;border:1px solid #E0E6ED;background-color:#fff;margin-bottom:30px
}
.w-panel__header{font-size:18px;height:50px
}
.w-panel__btns{position:absolute;top:30px;right:30px;z-index:1
}
.w-panel__btns .el-button--primary{background:#fff;border-color:#20a0ff;color:#20a0ff;padding-left:20px;padding-right:20px
}
.w-panel__btns [class^=el-icon-]{transform:scale(.7);position:relative;left:1px;top:1px
}


