
.w-tabs-link{display:inline-block
}
.w-tabs-link__item{float:left;position:relative;padding:0 16px;line-height:60px;font-size:16px;color:#8492a6;cursor:pointer
}
.w-tabs-link__item.is-active{color:#20a0ff
}
.w-tabs-link__active-bar{position:absolute;bottom:0;left:0;width:100%;height:3px;background-color:#20a0ff;z-index:1;transition:transform .3s cubic-bezier(.645, .045, .355, 1);list-style:none
}


