
.w-cascader__menu{display:inline-block;vertical-align:top;height:180px;list-style:none;margin:0;padding:0;border-right:1px solid #e9e9e9;overflow:auto
}
.w-cascader__menu:last-child{border-right-color:transparent;margin-right:-1px
}
.w-cascader__item{padding:8px 26px 8px 14px;position:relative;white-space:nowrap;font-size:14px;overflow:hidden;text-overflow:ellipsis;color:#475669;height:36px;line-height:1.5;cursor:pointer;min-width:120px
}
.w-cascader__item:hover{background-color:#e5e9f2
}
.w-cascader__item.selected{color:#fff;background-color:#20a0ff
}


